import CONF from './config'
import Api from '@/utils/api'

const login = CONF.login.API

export default {
  /**
   * [获取警员信息]
   * @param  {Object} params [description]
   * @return {[type]}        [description]
   */
  async getOfficerInfo(params = {}) {
    const res = await Api.get(login['getOfficerInfo'], params)
    return res.data
  },
  /**
   * [重置密码]
   * @param  {Object} params [description]
   * @return {[type]}        [description]
   */
  async resetPassword(params = {}) {
    const res = await Api.get(login['resetPassword'], params)
    return res.data
  },
  /**
   * [获取菜单信息]
   * @param  {Object} params [description]
   * @return {[type]}        [description]
   */
  async getMenuList(params = {}) {
    const res = await Api.get(login['getMenuList'], params)
    return res.data.map(v => v.name)
  }
}
