<template>
  <div class="login-wrapper">
    <el-form :model="loginForm" status-icon :rules="formRules" ref="loginForm" class="login-form">
      <el-form-item>
        <header class="login-title">宿迁人口管控系统</header>
      </el-form-item>
      <el-form-item prop="officerNo" :error="loginForm.officerNoErrMsg">
        <el-input type="text" v-model="loginForm.officerNo" autocomplete="off" clearable placeholder="警号"></el-input>
      </el-form-item>
      <el-form-item prop="password" :error="loginForm.passwordErrMsg">
        <el-input type="password" v-model="loginForm.password" autocomplete="off" clearable placeholder="密码"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" style="width: 100%;" @click="handleSubmit">登录</el-button>
      </el-form-item>
      <!-- <div class="login-reset">
        <el-button type="text" size="mini" @click="handleReset">忘记密码？</el-button>
      </div> -->
    </el-form>
  </div>
</template>

<script>
import md5 from 'blueimp-md5'
import Cookies from 'js-cookie'
import Service from './service'
import { getAuthorizedMenu } from '@/constants'

export default {
  name: 'LoginPage',
  data() {
    return {
      loginForm: {
        officerNo: '',
        password: '',
        officerNoErrMsg: '',
        passwordErrMsg: ''
      },
      formRules: {
        officerNo: [
          { required: true, message: '请输入警号', trigger: 'blur' },

        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ],
      },
      isLoading: false
    }
  },
  created() {
    if (Cookies.get('officerInfo')) {
      const officerInfo = JSON.parse(Cookies.get('officerInfo'))
      this.loginForm.officerNo = officerInfo.officerNo
    }
  },
  methods: {
    handleSubmit() {
      this.isLoading = true
      this.loginForm.officerNoErrMsg = ''
      this.loginForm.passwordErrMsg = ''
      this.$refs.loginForm.validate(async (valid) => {
        if (valid) {
          // this.handleLogin()
          const res = await this.getOfficerInfo()
          if (res) {
            this.handleLogin()
          } else {
            this.isLoading = false
          }
        } else {
          return false
        }
      })
    },
    async getOfficerInfo() {
      const { officerNo, password } = this.loginForm
      const params = { officerNo }
      const res = await Service.getOfficerInfo(params)
      if (res) {
        if (res.password !== md5(password)) {
          this.loginForm.passwordErrMsg = '密码错误!'
        } else {
          Cookies.set('officerNo', res.officerNo, { expires: 365 })
          Cookies.set('officerInfo', JSON.stringify(res), { expires: 365 })
          Cookies.set('isLogin', true, { expires: 365 })
        }
      } else {
        this.loginForm.officerNoErrMsg = '用户名不存在!'
      }
      return res.password === md5(password)
      return true
    },
    async handleReset() {
      if (this.loginForm.officerNo === '') {
        this.$message.warning('警号为空，请输入警号以重置密码。')
        return
      }
      const params = {
        officerNo: this.loginForm.officerNo
      }
      const res = await Service.resetPassword(params)
      if (res) {
        this.$message.success('密码重置成功，请重新登录。')
      }
    },
    async handleLogin() {
      const params = {
        officerNo: this.loginForm.officerNo
      }
      const res = await Service.getMenuList(params)
      const menuList = getAuthorizedMenu(res)
      Cookies.set('menuList', JSON.stringify(menuList), { expires: 365 })
      this.$router.push({
        name: 'Home',
        params: {
          officerNo: this.loginForm.officerNo
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
  .login-wrapper {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    min-height: 100%;
    background: url("~@/assets/img/bg.png") center center no-repeat;
    background-color: #1D1E74;
    .login-title {
      font-size: 20px;
      text-align: center;
    }
    .login-form {
      border-radius: 4px;
      padding: 24px 32px;
      width: 90%;
      max-width: 320px;
      background: #ffffff;
      position: relative;
      box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.3);
    }
    .login-reset {
      display: flex;
      justify-content: flex-end;
    }
  }
</style>
